import barba from "@barba/core";
// import barbaCss from "@barba/css";
import gsap from "gsap";

export default class Transition {
  constructor() {
    // barba.use(barbaCss);
    // barba.hooks.before((data) => {
    //   console.log(data);
    // });
    this.initTransitionPage();
  }

  animEnter(container) {
    console.log(container)
    return gsap.from(container, {
      autoAlpha: 0,
      duration: 2,
      clearProps: "all",
      ease: "none"
    });
  }
  animLeave(container) {
    return gsap.to(container, {
      autoAlpha: 0,
      duration: 2,
      clearProps: "all",
      ease: "none"
    });
  }

  initTransitionPage() {
    barba.init({
      transitions: [
        {
          name: "home",
          once({ next }) {
            console.log("once");
            this.animEnter(next);
          },
          leave: ({ current }) => this.animLeave(current.container),
          enter({ next }) {
            console.log("enter");
            this.animEnter(next);
          }
        }
      ]
    });
  }
}
